<template>
  <div id="Puzzlesapp">
    <!-- <v-stepper>
      <v-stepper-header>
        <v-stepper-step v-for="(item,index) in question"
                        :key="index"
                        :step="index + 1"
                        :complete="index == itemNum-1">
        </v-stepper-step>
      </v-stepper-header>
    </v-stepper> -->




    <!--<div class="num" :class="{'wronganswer': wrongAnswer}">{{itemNum + 1}}<span>/</span>{{ question.length}}</div>-->
    <div class="pionter">
      <div class="p" :class="{s:itemNum + 1>=1}"></div>
      <div class="l"></div>
      <div class="p" :class="{s:itemNum + 1>=2}"></div>
      <div class="l"></div>
      <div class="p" :class="{s:itemNum + 1>=3}"></div>
      <div class="l"></div>
      <div class="p" :class="{s:itemNum + 1>=4}"></div>
    </div>
    <div class="content">
      <div class="title" style="color: #5D8D89;font-size: 120%">{{ questionTitle }}</div>

      <div class="router-l" v-if="leave">
        <div class="t1">
          <p>progress for every question can not be saved, the system</p>
          <p>can only auto-save your progress for every chapter</p>
        </div>

        <div class="t2">
          <p>Please continue to answering question in this chapter or </p>
          <p>you will lose your progress</p>
        </div>

        <div class="btn-group">
          <div class="back btn" @click="routercancel()">Back &lt;</div>
          <div class="confirm btn" @click="go()">confirm -></div>
        </div>
      </div>


      <div class="getin" @click="togglePuzzle()" v-if="isPuzzle">
        <div class="puzzles puzzle1"
             :class="{'puzzleactive': puzzlecilick}"></div>
        <div class="puzzles puzzle2"
             :class="{'puzzleactive': puzzlecilick}"></div>
        <div class="puzzles puzzle3"
             :class="{'puzzleactive': puzzlecilick}"></div>
        <div class="bag"></div>
      </div>

      <div class="question_list" v-if="!leave">

        <!--<mt-checklist
                      align="right"
                      v-model="value"
                      :options=options
                      v-if="quesionStatus"
                      class="quesionCheck">
        </mt-checklist>-->
        <div class="checklist" v-if="quesionStatus">
          <div class="list" v-for="(item,i) in options" :key="i">
            <div class="text">
              {{item.label}}
              <div class="sel" :class="{dui:value.includes(item.value)}" @click="select(item.value)"></div>
            </div>
            <img :src='require(`./assets/qu/${Number(id)}.${itemNum+1+item.value}.png`)' alt="" class="po">
          </div>
        </div>


        <!--<div class="resultlist"
             v-if="isResult">
          <div class="item"
               v-for="(item,index) in options"
               :key="index"
               @click="toWhy(index)">
            <span class="content">{{ item.label }}</span>
            <span class="bg"
                  :class="{'right': item.correct}">!</span>
          </div>
        </div>-->
        <div class="resultlist-new"
             v-if="isResult">
          <div class="list" v-for="(item,index) in options" :key="i">
            <div class="icon">
              <img src="./assets/y-d.png" alt="" v-if="item.correct" @click="toWhy(index)">
              <img src="./assets/r-g.png" alt="" v-if="!item.correct" @click="toWhy(index)">
            </div>
            <div class="text">
              {{item.label}}
            </div>
            <img :src='require(`./assets/qu/${Number(id)}.${itemNum+1+item.value}.png`)' alt="" class="po">
          </div>
        </div>


        <div class="resu success" v-show="whyflag">
          <div class="container">
            <div class="content">{{why}}</div>
            <div class="btn-group">
              <div class="more"
                   @click="toMore">Read more<img src="./assets/s.png" class="iicco-s" style="width: 38px;height: auto;margin-left: 15px;vertical-align: middle;" alt=""></div>
              <div class="next"
                   @click="toNext">Confirm</div>
            </div>
          </div>
        </div>

        <div class="resu success" v-show="success">
          <div class="container">
            <div class="content">
              <div class="title">
                Congratulations you choosse the right answer
              </div>
              <div class="msg">
                you could learn more information on tips or collect the puzzle now
              </div>
            </div>
            <div class="btn-group">
              <div @click="toResult">
                Find out why
                <img class="iicco-f-d" src="./assets/f-d.png" style="width: 38px;height: auto;margin-left: 15px;vertical-align: middle;" alt="">
              </div>
              <div @click="nextQ">Confirm <span>→</span></div>
            </div>
          </div>

        </div>

        <div class="resu wrong" v-show="wrongflag">
          <div class="container">
            <div class="content">
              <div class="title">
                Sorry you choose the wrong answer
              </div>
              <div class="msg">
                you could know more information on tips of why your answer are wrong ot try it again by yourslef
              </div>
            </div>
            <div class="btn-group">
              <div @click="toResult">Find out why<img class="iicco-f-g" src="./assets/f-g.png" style="width: 32px;height: auto;margin-left: 15px;vertical-align: middle;" alt=""></div>
              <div @click="wrongConfirm">Try again <span>→</span></div>
            </div>
          </div>
        </div>

        <v-btn type="primary"
               :loading="loading"
               @click="nextItem"
               class="btn"
               :disabled="value == ''"
               style="border-color: rgba(255,255,255,0.68);background: rgba(230,230,230,0.4);padding: 0 2rem;position: absolute;bottom: 50px;right: 110px;"
               v-if="quesionStatus">
          <span v-if="!loading">Confirm1</span>
          <span v-else>Loading...</span>
        </v-btn>


<!--        <v-btn type="primary"
               :loading="loading"
               @click="resultNextItem"
               class="btn"
               style="border-color: rgba(255,255,255,0.68);background: rgba(230,230,230,0.4);padding: 0 2rem;position: absolute;bottom: 50px;right: 110px;"
               v-if="whyBtn">
          <span v-if="!loading">hahhahhah </span>
          <span v-else>Loading...</span>

        </v-btn>-->

        <v-btn v-show="itemNum == 4 || false" @click="Confirms()">  hahhahahh</v-btn>

      </div>
    </div>
  </div>
</template>

<script>
  export default {
    beforeRouteLeave(to, from, next) {
      this.toPath = to.path
      if ((this.itemNum === this.question.length - 1)) {
        next()
      }
      if (this.sureleave) {
        next()
      }
      this.leave = !this.leave
    },
    name: 'Puzzles',
    data: () => ({
      id: '', // page id
      question: {}, // questions object
      itemNum: 0, // itme number                        //题下标
      subData: [], // Data for each question
      questionId: '', // quesion ID
      questionTitle: '', // question title
      options: [], // the answer of question
      multi: false, // multi?
      value: [], // Value carried by option
      btn: 'confirm', // the texxt of button
      right: '', // the right option
      loading: false, // loading animation
      quesionStatus: true, // quesion list
      success: false, // choose the right answer
      msg: [], // the answer messgae
      _id: [],
      wrongAnswer: false, // the color of stpes
      isResult: false, // the reult box
      localSnum: 0, // read the localstorage
      whyflag: false, // show me why?
      why: '', // tell me why
      $a: '', // further
      wrongflag: false, // choose wrong answer?
      whyBtn: false,
      leave: false,
      sureLeave: false,
      toPath: '',
      puzzlecilick: false,
      isPuzzle: false
    }),
    async created() {
      /** Got the page id to request the question lists */
      this.id = this.$route.query.id
      this.localSnum = Number(localStorage.getItem('Checkpoint'))
      if (this.localSnum == 0) {
        this.localSnum = 1
      }
      this.Userpuzzles = Number(localStorage.getItem('puzzles'))
      // console.log(this.localSnum, this.id)
      this.$http.get(this.localConfig.question).then(res => {
        /** Get different questions according to different ID values */
        switch (this.id) {
        case '01':
          this.question = res.data.results.list01
          this._setData();
          break
        case '02':
          this.question = res.data.results.list02
          this._setData();
          break
        case '03':
          this.question = res.data.results.list03
          this._setData();
          break
        }

      })


    },
    methods: {
      /** Take out the key value pairs in the object */
      _setData() {
        this.subData = this.question[this.itemNum];
        this.questionId = this.subData.id;
        this.questionTitle = this.subData.title;
        this.options = this.subData.answer;
        this.right = this.subData.right
        console.log(this.options)
      },
      nextItem() {
        /** loading animation */
        this.loading = true
        setTimeout(() => {
          this.loading = false
        }, 500);
        setTimeout(() => {
          /** Try to run the following code and catch the error */
          try {
            /** Confirm user selected option */
            if (this.value) {
              console.log(this.value.sort().toString(),'我选的答案')
              console.log(this.right.sort().toString(),'该有的正确答案')
              /** Check if it contains the correct answer */
              // this.right.indexOf(this.right.indexOf(this.value) != -1
              if (this.value.sort().toString() == this.right.sort().toString()) {
                console.log(this.wrongAnswer,'this.wrongAnswer')
               /* this.wrongAnswer = false*/
                /** submit */
                if (this.itemNum === this.question.length - 1) {
                  console.log(this.question.length,'this.question.length2')
                  console.log(this.id,'this.id')
                  console.log(this.localSnum,'this.localSnum')
                  if (Number(this.id) < this.localSnum + 1) {
                    localStorage.setItem("Checkpoint", Number(this.id) + 1);
                    localStorage.setItem("puzzles", Number(this.Userpuzzles + 12))
                  }

                  if(this.$route.query.id == '01'){
                    let s = 2
                    sessionStorage.setItem("lockflagvalue",s)
                    this.$router.push({path: '/index/game',query:{ids:1}});
                  }
                  if(this.$route.query.id == '02'){
                    let s = 3
                    sessionStorage.setItem("lockflagvalue",s)
                    this.$router.push({path: '/index/game',query:{ids:1}});
                  }
                  if(this.$route.query.id == '03'){
                    let s = 4
                    sessionStorage.setItem("lockflagvalue",s)
                    this.$router.push({path: '/index/game',query:{ids:1}});
                  }


                  /** I will set localstorage of here ... */
                } else {
                  this.success = !this.success
                  this.quesionStatus = false
                }
                if (this.itemNum < this.question.length - 1) {
                  return false;
                } else {
                  this.btn = 'submit';
                }
              } else {
                this.whyBtn = true
                this.wrongflag = true
   /*             this.quesionStatus = false*/
              }

            } else {
              alert('请选择选项');
            }
          } catch (e) {
            alert('Maybe something went wrong')
          }
        }, 700)
      },
      nextQ() {
        this.value = []
        this.isPuzzle = true
        this.success = false
        this.itemNum += 1;
        this._setData();
      },
      toWhy(i) {
        this.$a = this.options[i].further
        this.why = this.options[i].msg // content of the answer
        this.whyflag = true // show the answer box
        this.isResult = !this.isResult // close the question list
        // this.whyBtn = !this.whyBtn
        this.whyBtn = false
      },
      toMore() {
        /** 跳转到问题链接 */
        window.open(this.$a, '_blank').location;
      },
      toNext() {
        this.whyflag = false // show the answer box
        this.isResult = !this.isResult // close the question list
        this.whyBtn = true
      },
      resultNextItem() {
        this.isResult = false
        this.loading = true
        setTimeout(() => {
          this.loading = false
        }, 500);
        setTimeout(() => {
          this.nextQ()
          this.whyBtn = false
        }, 700)
      },
      /** to resut page */
      toResult() {
        this.isResult = true
        this.wrongflag = false
        this.whyBtn = true
        this.success = false
      },
      /** try again */
      wrongConfirm() {
        this.wrongflag = false
        this.quesionStatus = true
        this.whyBtn = true
      },
      go() {
        this.sureleave = true
        this.$router.push(this.toPath)
      },
      routercancel() {
        this.leave = !this.leave
      },
      togglePuzzle() {
        this.puzzlecilick = !this.puzzlecilick

        setTimeout(() => {
          this.isPuzzle = false
          this.quesionStatus = true
        }, 500)
      },
      select(v){
        if(this.value.includes(v)){
          this.value=this.value.filter(i => i!==v)
        }else {
          this.value.push(v)
        }
      }
    },
    watch: {
      /** selcet */
      /** 监听用户的选择 */
      value() {
        this._id = []
        if (this.value.indexOf('A') > -1) {
          this._id.push(0)
        }
        if (this.value.indexOf('B') > -1) {
          this._id.push(1)
        }
        if (this.value.indexOf('C') > -1) {
          this._id.push(2)
        }
        if (this.value.indexOf('D') > -1) {
          this._id.push(3)
        }
        if (this.value.indexOf('E') > -1) {
          this._id.push(4)
        }
        if (this.value.indexOf('F') > -1) {
          this._id.push(5)
        }
      }
    }
  }
</script>

<style lang="less">
