        .list{width: 100%;}
      }
    }
    .resultlist-new{
      flex: 1;
      display: flex;
      flex-wrap: wrap;
      .list{
        width: 50%;
        display: flex;
        align-items: center;
        padding: 20px;
        .icon{
          padding-right: 10px;
          img{
            width: 40px;
          }
        }
        .text{
          background: rgba(187, 203, 201 , 0.8);
          border-radius: 3px;
          color: #fff;
          padding: 15px;
          flex: 1;
          font-size: 16px;
          line-height: 24px;
          position: relative;
          padding-right: 50px;
          min-height: 95px;
        }
        img.po{
          width: 150px;
          height: 150px;
        }
      }
      @media screen and (max-width:870px) {
        .list{width: 100%;}
      }
    }
    .content {
