        }
      }

      .router-l {
        width: 60vw;
        height: 40vh;
        text-align: center;
        margin: 10vh auto;
        background: #747270;
        padding: 3vh 0;

        @media screen and (max-width:1024px) {
          width: 90vw;
        }

        .t1 {
          color: #fff;
          font-size: 1.5rem;
          line-height: 2rem;
          margin-bottom: 4vh;

          @media screen and (max-width:1024px) {
            font-size: 1rem;
            line-height: 1.4rem;
          }
        }

        .t2 {
          color: #fff;
          font-size: 1.5rem;
          line-height: 2rem;
          margin-bottom: 4vh;

          @media screen and (max-width:1024px) {
